:root {
  --elk-side-bg-color: #ffffff;
  --side-nav-a-color: #495057;
  --side-nav-a-hover-color:  #0c8599;
  --side-nav-a-hover-bg-color: #e9ecef;
  --elk-header-bg-color: #ffffff;
  --top-nav-a-color: #495057;
  --top-nav-a-hover-color: #0c8599;
  --top-nav-a-hover-bg-color: #e9ecef;
  --elk-switch-indicator-bg: #e9ecef;
  --elk-switch-indicator-border: #dee2e6;
  --elk-switch-indicator-before: #ffffff;
  --elk-switch-indicator-checked-bg: #e9ecef;
}

[data-theme="dark"] {
  --elk-side-bg-color: #343a40;
  --side-nav-a-color: #868e96;
  --side-nav-a-hover-bg-color: #212529;
  --elk-header-bg-color: #343a40;
  --top-nav-a-color: #868e96;
  --top-nav-a-hover-bg-color: #212529;
  --elk-switch-indicator-bg: #adb5bd;
  --elk-switch-indicator-border: #343a40;
  --elk-switch-indicator-before: #ffffff;
  --elk-switch-indicator-checked-bg: #adb5bd;
}

.elk-app,
body > .ember-view {
  position: absolute;
  width: 100%;
  display: flex;
  flex-direction: row;
  min-height: 100%;
}

.elk-wrap {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
}

.elk-header {
  position: relative;
}

.elk-container {
  display: flex;
  flex: 1 1 auto;
  flex-basis: auto;
  flex-direction: row;
  position: relative;
}

.elk-main {
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1 1 0%;
  flex-basis: 0%;
  transition: margin 0.3s ease-out;
}

.elk-main-content {
  position: relative;
  margin-bottom: auto;
  flex: 1 1 auto;
  flex-basis: auto;
  padding: 1rem;
}

.elk-side {
  display: flex;
  flex-flow: column nowrap;
  margin: 0;
}

/*
* <div class="elk-app is-fixed">
*
*/
.elk-app.is-fixed {
  height: 100%;
}

.elk-app.is-fixed .elk-container,
.elk-app.is-fixed .elk-side,
.elk-app.is-fixed .elk-main {
  overflow-y: auto;
}

/*
* <div class="elk-app is-fixed">
*   <div class="elk-main is-fixed">
*     ...
*   </div>
* </div>
*
*/
.elk-app.is-fixed .elk-main.is-fixed .elk-main-content {
  overflow-y: auto;
}

/*
* elk-side : inspired .onoffcanvas.is-left
*
* available classess: is-open
*/
.elk-side {
  width: 256px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  transform: translate3d(-100%, 0, 0);
  transition: transform 0.3s ease-out;
}

/*
* show elk-side
*/
.elk-side.is-open {
  z-index: 1000;
  transform: translate3d(0, 0, 0);
}

@media (min-width: 992px) {
  .elk-side.is-open ~ .elk-main {
    margin-left: 256px;
  }
}

/*
* side-content
*
* available classess: is-sticky
*/
.elk-side-content {
  margin-bottom: auto;
}

.elk-side-nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.elk-side-nav .metismenu {
  display: flex;
  flex-direction: column;
}

.elk-side-nav .metismenu li {
  margin-top: 5px;
}

.elk-side-nav .metismenu li:first-child {
  padding-top: 5px;
}

.elk-side-nav .metismenu li:last-child {
  padding-bottom: 5px;
}

.elk-side-nav .metismenu > li {
  position: relative;
}

.elk-side-nav .metismenu a {
  font-size: 0.875rem;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  outline-width: 0;
  transition: all 0.3s ease-out;
}

.elk-side-nav .metismenu a:active, .elk-side-nav .metismenu a:focus, .elk-side-nav .metismenu a:hover {
  text-decoration: none;
}

.elk-side-nav .metismenu ul a {
  padding: .5em;
  padding-left: 3.5rem;
}

.elk-side-nav .metismenu ul ul a {
  padding-left: 4.5rem;
}

/*
* .elk-side.is-mini
*/
@media (min-width: 992px) {
  .elk-side.is-mini {
    width: 56px;
    border-width: 0 !important;
  }
  .elk-side.is-mini.is-open ~ .elk-main {
    margin-left: 56px;
  }
  .elk-side.is-mini .metismenu li:last-child {
    padding-bottom: 0;
  }
  .elk-side.is-mini .metismenu > li > a.has-arrow::after {
    border-width: 0;
  }
  .elk-side.is-mini .nav-title {
    display: none;
    margin-left: 1.5rem;
  }
  .elk-side.is-mini .metismenu ul ul a {
    padding-left: 2.5rem;
  }
  .elk-side.is-mini .metismenu > li > ul {
    position: absolute;
    top: 100%;
    left: 100%;
    display: none;
  }
  .elk-side.is-mini .metismenu > li > ul > li > a {
    padding: 0.5em 0.5em 0.5em 1.5em;
  }
  .elk-side.is-mini .metismenu > li:hover > ul {
    display: block;
    min-width: 201px;
    height: auto !important;
  }
  .elk-side.is-mini .metismenu > li:hover > a {
    display: flex;
    flex-direction: row;
    min-width: 257px;
  }
  .elk-side.is-mini .metismenu > li:hover .nav-title {
    display: inline-block;
    align-self: center;
  }
  .elk-side.is-mini .metismenu > li:hover > .has-arrow::after {
    border-width: 1px 0 0 1px;
    transform: rotate(-135deg);
  }
}

/*
* .elk-side.is-hoverable
*/
@media (min-width: 992px) {
  .elk-side.is-hoverable {
    transition: all 0.3s ease;
    width: 56px;
  }
  .elk-side.is-hoverable.is-open ~ .elk-main {
    margin-left: 56px;
  }
  .elk-side.is-hoverable .metismenu > li > a.has-arrow::after {
    transition: none;
    border-width: 0;
  }
  .elk-side.is-hoverable .metismenu > li > ul.collapse.in {
    display: none;
  }
  .elk-side.is-hoverable .nav-title {
    transition: transform 0.3s ease;
    transform: translate3d(-10000%, 0, 0);
  }
  .elk-side.is-hoverable:hover {
    width: 256px;
  }
  .elk-side.is-hoverable:hover .nav-title {
    transform: translate3d(0, 0, 0);
  }
  .elk-side.is-hoverable:hover .metismenu > li > ul.collapse.in {
    display: block;
  }
  .elk-side.is-hoverable:hover .metismenu > li > a.has-arrow::after {
    border-width: 1px 0 0 1px;
  }
}

.onoffcanvas-toggler {
  display: inline-block !important;
  outline-width: 0;
}

.elk-top-nav {
  padding: 0 1rem;
}

.elk-top-nav.is-open {
  position: relative;
}

.elk-top-nav ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.elk-top-nav .metismenu {
  display: flex;
  flex-direction: column;
}

.elk-top-nav .metismenu .has-icon {
  height: auto;
}

.elk-top-nav .metismenu > li {
  display: flex;
  flex-direction: column;
  position: relative;
}

.elk-top-nav .metismenu a {
  font-size: 0.875rem;
  position: relative;
  display: block;
  padding: .75rem 1rem;
  outline-width: 0;
  transition: all 0.3s ease-out;
}

.elk-top-nav .metismenu ul a {
  padding: .5rem 5rem;
}

.elk-top-nav .metismenu a:active,
.elk-top-nav .metismenu a:focus,
.elk-top-nav .metismenu a:hover {
  text-decoration: none;
}

@media (min-width: 992px) {
  .elk-top-nav .metismenu {
    flex-direction: row;
  }
  .elk-top-nav .metismenu > li > a {
    padding-right: 2rem;
  }
  .elk-top-nav .metismenu > li {
    flex-direction: column;
  }
  .elk-top-nav .metismenu ul a {
    padding: .625rem 1rem;
  }
  .elk-top-nav .metismenu > li > ul {
    position: absolute;
    top: 100%;
    min-width: 256px;
    z-index: 1001;
  }
  .elk-top-nav .metismenu > li:last-child > ul {
    right: 0;
  }
  .elk-top-nav.is-hoverable .metismenu > li > ul {
    height: auto !important;
  }
  .elk-top-nav.is-hoverable .metismenu > li:hover > ul {
    display: block;
  }
  .elk-top-nav.is-hoverable .metismenu > li:hover > a.has-arrow:after {
    -webkit-transform: rotate(-135deg);
    transform: rotate(-135deg);
  }
}

.elk-header {
  background: #ffffff;
  background: var(--elk-header-bg-color);
}

/* .elk-top-nav colors */
.elk-header .elk-top-nav a,
.elk-header .elk-top-nav li,
.elk-header .elk-top-nav ul {
  background: #ffffff;
  background: var(--elk-header-bg-color);
}

.elk-header .elk-top-nav .metismenu a {
  color: #495057;
  color: var(--top-nav-a-color);
}

.elk-header .elk-top-nav .metismenu a:active, .elk-header .elk-top-nav .metismenu a:focus, .elk-header .elk-top-nav .metismenu a:hover {
  color: #0c8599;
  color: var(--top-nav-a-hover-color);
  background: #e9ecef;
  background: var(--top-nav-a-hover-bg-color);
}

.elk-header .elk-top-nav .metismenu li > a.active {
  color: #0c8599;
  color: var(--top-nav-a-hover-color);
  background: #e9ecef;
  background: var(--top-nav-a-hover-bg-color);
}

.elk-side {
  background: #ffffff;
  background: var(--elk-side-bg-color);
}

/* .elk-side-nav colors */
.elk-side .elk-side-nav a,
.elk-side .elk-side-nav li,
.elk-side .elk-side-nav ul {
  background: #ffffff;
  background: var(--elk-side-bg-color);
}

.elk-side .elk-side-nav .metismenu a {
  color: #495057;
  color: var(--side-nav-a-color);
}

.elk-side .elk-side-nav .metismenu a:active, .elk-side .elk-side-nav .metismenu a:focus, .elk-side .elk-side-nav .metismenu a:hover {
  color: #0c8599;
  color: var(--side-nav-a-hover-color);
  background: #e9ecef;
  background: var(--side-nav-a-hover-bg-color);
}

.elk-side .elk-side-nav .metismenu li > a.active {
  color: #0c8599;
  color: var(--side-nav-a-hover-color);
  background: #e9ecef;
  background: var(--side-nav-a-hover-bg-color);
}

.elk-switch-indicator {
  background: #e9ecef;
  background: var(--elk-switch-indicator-bg);
  border: 1px solid #dee2e6;
  border: 1px solid var(--elk-switch-indicator-border);
}

.elk-switch-indicator::before {
  background: #ffffff;
  background: var(--elk-switch-indicator-before);
}

.elk-switch-input:checked ~ .elk-switch-indicator {
  background: #e9ecef;
  background: var(--elk-switch-indicator-checked-bg);
}

.is-sticky {
  position: sticky;
  top: 0;
  z-index: 1002;
}

.is-sticky.is-bottom {
  top: auto;
  bottom: 0;
}

.has-icon {
  width: 56px;
  flex: 0 0 56px;
  height: 2.5rem;
  font-size: 1.5em;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.onoffcanvas {
  visibility: hidden;
}

.onoffcanvas.is-open {
  visibility: visible;
}

.elk-switch {
  user-select: none;
  cursor: default;
  display: inline-flex;
  align-items: center;
  margin: 0;
}

.elk-switch-indicator {
  display: inline-block;
  height: 1.25rem;
  width: 2.25rem;
  border-radius: 50px;
  position: relative;
  vertical-align: bottom;
  transition: .3s border-color, .3s background-color;
}

.elk-switch-indicator::before {
  content: '';
  position: absolute;
  height: calc(1.25rem - 4px);
  width: calc(1.25rem - 4px);
  top: 1px;
  left: 1px;
  border-radius: 50%;
  transition: .3s left;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.4);
}

.elk-switch-input {
  position: absolute;
  z-index: 9;
  opacity: 0;
}

.elk-switch-input:checked ~ .elk-switch-indicator::before {
  left: calc(1rem + 1px);
}

